import React, {useContext, useEffect, useState} from "react";
import {useNavigate} from "react-router-dom";
import Logo from "./assets/images/logo.png";
import {SurveyContext} from "./surveyContext";
import {Controller, useForm} from "react-hook-form";
import axios from "axios";
import {db, fetchQuestions, getStoredQuestions, syncSubmissions} from "./db";
import {toast} from "react-toastify";


const useOnline = () => {
    const [isOnline, setIsOnline] = useState(navigator.onLine);
    useEffect(() => {
        const handleOnline = () => setIsOnline(true);
        const handleOffline = () => setIsOnline(false);

        window.addEventListener('online', handleOnline);
        window.addEventListener('offline', handleOffline);

        return () => {
            window.removeEventListener('online', handleOnline);
            window.removeEventListener('offline', handleOffline);
        };


    }, []);

    return isOnline;
};


const SurveyEntry = () => {
    const navigate = useNavigate();
    const {setSurveyCompleted} = useContext(SurveyContext);
    const {control,register, handleSubmit, watch, formState: {errors}} = useForm();
    const isOnline = useOnline();
    const [savedEmails, setSavedEmails] = useState([]);
    const [questions, setQuestions] = useState([]);

    useEffect(() => {
        if (isOnline) {
            syncSubmissions();
        }
    }, [isOnline]);


    useEffect(() => {
        // Call the function to fetch saved emails when the component mounts
        fetchEmailsFromApi()

        if (!isOnline) {
            fetchSavedEmails()

        }

    }, []);

    const fetchSavedEmails = async () => {
        // Function to fetch saved emails from the database and update state
        const emails = await db.emails.toArray();
        setSavedEmails(emails);
    };

    const fetchEmailsFromApi = async () => {
        try {
            const response = await axios.get('https://admin-panel.masscom-ci.com/api/answers'); // Replace with your API endpoint
            const emailsFromApi = response.data?.data?.emails; // Assuming the API response is an array of emails
            console.log(response.data)
            await saveEmailsToDB(emailsFromApi);
            await fetchSavedEmails();
        } catch (error) {
            console.error('Error fetching emails from API:', error);
        }
    };

    const saveEmailsToDB = async (emails) => {
        // Loop through the array of emails and save them to the Dexie database
        for (const email of emails) {
            await db.emails.add({email});
        }
    };

    useEffect(() => {
        const fetchSurveyQuestions = async () => {
            if (isOnline) {
                await fetchQuestions();
            }

            const storedQuestions = await getStoredQuestions();


            setQuestions(storedQuestions);
        };
        console.log("isOnline", isOnline)
        fetchSurveyQuestions();
    }, [isOnline, navigate]);


    const onSubmit = async (e) => {
        try {
            const email = e?.email
            const body = {email: email};
            const emails = await db.emails.toArray();

            if (!isOnline) {
                const isAlready = emails?.find((v) => v?.email === email)


                console.log("isAlready", isAlready)

                if (isAlready) {
                    toast.error("Accdez aux vidos /Playlists")
                    setSurveyCompleted(true)
                    navigate({
                        pathname: '/video'
                    });
                    return;
                }
                toast.error('Formulaire soumis hors ligne. Il sera synchronis lorsque le rseau sera disponible.');
                navigate({
                    pathname: '/survey', search: `?email=${email}`,
                });
                return;
            }

            const response = await axios.post('https://admin-panel.masscom-ci.com/api/check_email', body)

            if (response?.data?.status) {
                setSurveyCompleted(true)
                navigate({
                    pathname: '/survey', search: `?email=${email}`,
                });
            } else {
                toast.success("Accdez aux vidos /Playlists")
                setSurveyCompleted(true)
                navigate({
                    pathname: '/video'
                });
            }
        } catch (error) {
            // toast.error(error?.response?.data?.message);
            setSurveyCompleted(true)
            navigate({
                pathname: '/video'
            });
        }
    }

    return (<>
        <div style={{
            width: '100vw',
            height: '100vh',
            backgroundPosition: "center",
            backgroundImage: `url(${questions[0]?.company_bg})`
        }}>
            <nav style={{textAlign: "center"}}>
                <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
            </nav>
            <div className="quiz-container">
                <p>Entrez votre Email ou numro et Accdez  nos films / Vidos / Liste de musique </p>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <div className={"flex flex-col my-4"}>
                        <label htmlFor="email" className={"font-bold"}>Email ou numro et Accdez</label>
                        <Controller
                            name="email"
                            control={control}
                            rules={{
                                required: 'This field is required',
                                validate: (value) => {
                                    // Define a regex pattern for phone numbers and email addresses
                                    const regex = /^(?:\d{10}|[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,})$/;
                                    return regex.test(value) || 'Invalid input';
                                },
                            }}
                            render={({ field }) => (
                                <>
                        <input {...register("email", {required: true})} type="text"
                               id="email" name="email"
                               placeholder=""/>
                                    {errors.email && (
                                        <p className="error-message text-red-400">
                                            {errors.email.message}
                                        </p>
                                    )}
                                </>
                            )}
                        />
                    </div>

                    <button type="submit">Submit</button>
                </form>
            </div>
        </div>
    </>)
}

export default SurveyEntry