import Logo from "./assets/images/logo.png";
import React, {useContext, useEffect, useState} from "react";
import {Link} from "react-router-dom";
import {useOnline} from "./video.page";
import {fetchQuestions, getStoredQuestions} from "./db";
import {SurveyContext} from "./surveyContext";

const YoutubeVideoFolderPage = () => {
    const isOnline = useOnline();
    const {surveyCompleted} = useContext(SurveyContext);

    const [videos, setVideos] = useState([]);
    const [questions, setQuestions] = useState([]);

    useEffect(() => {
        async function fetchCachedVideo() {
            const cacheName = 'my-video-cache'; // Replace with your cache name from the service worker
            const videoUrl1 = '/videos/video-1.mp4'; // Replace with the actual video file path
            const videoUrl2 = '/videos/video-2.mp4'; // Replace with the actual video file path
            const videoUrl3 = '/videos/video-3.mp4'; // Replace with the actual video file path
            const videoUrl4 = '/videos/video-4.mp4'; // Replace with the actual video file path

            const videos = [
                {
                    url: videoUrl1,
                    title: "Video 1"
                },
                {
                    url: videoUrl2,
                    title: "Video 2"
                },
                {
                    url: videoUrl3,
                    title: "Video 3"
                },
                {
                    url: videoUrl4,
                    title: "Video 4"
                }
            ]
            setVideos(videos)
        }

        fetchCachedVideo();
    }, []);

    useEffect(() => {
        const fetchSurveyQuestions = async () => {
            if (isOnline) {
                await fetchQuestions();
            }
            const storedQuestions = await getStoredQuestions();
            setQuestions(storedQuestions);
        };
        fetchSurveyQuestions();
    }, [isOnline]);

    return (
        <>

            {surveyCompleted ?
                <>
                    <nav style={{textAlign: "center"}}>
                        <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
                    </nav>
                    <div className={"my-10"}>
                        <Link to={'/video'}>
                            <button className={"bg-indigo-400 text-white px-2 py-1 rounded-lg"}>
                                {"<< Go Back"}
                            </button>
                        </Link>
                    </div>
                    <div className="md:w-[38rem] w-[25rem] theme-structure big-file-manager">
                        <div className={"text-center flex items-center justify-evenly"}>
                            <div className={"hover:animate-pulse hover:cursor-pointer flex flex-col items-center"}>
                                <div className="video-list">

                                    <div className="px-4  py-4 video-container grid grid-cols-1 lg:grid-cols-3 gap-4">
                                        {videos.map((video) => (
                                            <div className="hover:animate-pulse  video-item" key={video.id}>
                                                <video controls>
                                                    <source src={video.url} type="video/mp4" />
                                                    Your browser does not support the video tag.
                                                </video>
                                                <p>{video?.title}</p>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </>
             : <div className={"absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2"}>
                    <nav style={{textAlign: "center"}}>
                        <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
                    </nav>
                    <div className={"px-5 py-4 text-center bg-amber-50 rounded-2xl"}>
                        <p>
                            Accs refus, veuillez d'abord faire une enqute
                        </p>

                        <div>
                            <Link to={'/'}>
                                <button className={"bg-indigo-400 rounded-2xl px-3 py-2 my-4 text-white"}>
                                    Got to survey
                                </button>
                            </Link>
                        </div>

                    </div>
                </div> }
        </>
    )
}

export default YoutubeVideoFolderPage