import React, {useContext, useState} from 'react';
import Logo from "./assets/images/logo.png";
import {ListItem, ListItemButton} from "@mui/material";
import Typography from "@mui/material/Typography";
import {List} from "reactstrap";

import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import PauseIcon from '@mui/icons-material/Pause';
import MusicNoteIcon from '@mui/icons-material/MusicNote';
import {Link} from "react-router-dom";
import {SurveyContext} from "./surveyContext";

const MusicFolder = () => {
    const [currentTrack, setCurrentTrack] = useState(null);
    const {surveyCompleted} = useContext(SurveyContext);

    const tracks = [
        { id: 1, title: 'Track 1', src: 'https://commondatastorage.googleapis.com/codeskulptor-assets/Collision8-Bit.ogg' },
        { id: 2, title: 'Track 2', src: 'https://commondatastorage.googleapis.com/codeskulptor-assets/week7-bounce.m4a' },
        { id: 3, title: 'Track 3', src: 'https://commondatastorage.googleapis.com/codeskulptor-assets/Collision8-Bit.ogg' },
        { id: 4, title: 'Track 4', src: 'https://commondatastorage.googleapis.com/codeskulptor-assets/Collision8-Bit.ogg' },
    ];

    const playTrack = (track) => {
        if (currentTrack === track) {
            setCurrentTrack(null); // Pause if same track is clicked again
        } else {
            setCurrentTrack(track);
        }
    };
    return (
        <>
            {surveyCompleted ?
                <>
            <nav style={{textAlign: "center"}}>
                <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
            </nav>
            <div className={"my-10"}>
                <Link to={'/video'}>
                    <button className={"bg-indigo-400 text-white px-2 py-1 rounded-lg"}>
                        {"<< Go Back"}
                    </button>
                </Link>
            </div>
            <div className="md:w-[38rem] w-[25rem] theme-structure big-file-manager">
                <div className={"text-center flex items-center justify-evenly"}>
                    <div className={"hover:animate-pulse hover:cursor-pointer flex flex-col items-center"}>
                        <div className="music-player">
                            <List className={"flex justify-evenly"}>
                                {tracks.map((track) => (
                                    <ListItem key={track.id}>
                                        <ListItemButton onClick={() => playTrack(track)}>
                                            <MusicNoteIcon style={{ marginRight: '8px', fontSize: 24, color: currentTrack === track ? 'green' : 'black' }} />
                                            <Typography variant="body1">
                                                {track.title} {currentTrack === track && '(Playing)'}
                                            </Typography>
                                        </ListItemButton>
                                    </ListItem>
                                ))}
                            </List>
                            {currentTrack && (
                                <audio controls autoPlay>
                                    <source src={currentTrack.src} type="audio/mpeg" />
                                    Your browser does not support the audio element.
                                </audio>
                            )}
                        </div>
                    </div>
                </div>
            </div> </>
            : <div className={"absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2"}>
                <nav style={{textAlign: "center"}}>
                    <img src={Logo} style={{width: '200px', height: "200px", margin: "auto"}} alt={"logo"}/>
                </nav>
                <div className={"px-5 py-4 text-center bg-amber-50 rounded-2xl"}>
                    <p>
                        Accs refus, veuillez d'abord faire une enqute
                    </p>

                    <div>
                        <Link to={'/'}>
                            <button className={"bg-indigo-400 rounded-2xl px-3 py-2 my-4 text-white"}>
                                Got to survey
                            </button>
                        </Link>
                    </div>

                </div>
            </div> }
        </>

    );
};

export default MusicFolder;